import {
    normalizeElement,
    normalizeNodeList,
    ReplacementParser
} from "../ReplacementParser.js";

export class SeriousEats extends ReplacementParser {
    author() {
        const ele = this.querySelector(".lifestyle-food-bylines a");
        return normalizeElement(ele);
    }

    title() {
        const ele = this.querySelector("h1");
        return normalizeElement(ele);
    }

    ingredients() {
        const eles = this.querySelectorAll(".ingredient-list li");
        return normalizeNodeList(eles);
    }

    instructions() {
        const eles = this.querySelectorAll(".section--instructions p");
        return normalizeNodeList(eles);
    }

    yields() {
        const ele = this.querySelector(".recipe-serving, .recipe-yield");
        return normalizeElement(ele);
    }
}
